.FileInput {
  position: relative;
  width: 262px;
  height: 232px;
  background-color: #d9d9d9;
  overflow: hidden;
  margin: 0 auto;
}

.FileInput-preview {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.FileInput-preview.selected {
  opacity: 0.48;
  /* object-fit: cover; */
}

.FileInput-hidden-overlay {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.FileInput-clear-button {
  position: absolute;
  top: 9px;
  right: 9px;
  width: 26px;
  height: 26px;
  padding: 7px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
}

.register {
  position: absolute;
  width: 262px;
  height: 30px;
  background-color: #ff9b50;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 1s ease;
  transform: translateY(202px);
}
